import { useState, useEffect } from "react";
import React,{Component} from 'react';
import './Sec.scss';


function Sect01 () {
  const [ScrollY, setScrollY] = useState(0); 
  const [ScrollActive, setScrollActive] = useState(false);
  function  handleScroll() {
    if(ScrollY > (document.querySelector('.section01').offsetTop / 2)) {
      setScrollY(window.pageYOffset);
      setScrollActive(true);
    } else {
      setScrollY(window.pageYOffset);
      setScrollActive(false);
    }    
  }
  // 스크롤시 메뉴 고정
  useEffect(() => {
    function scrollListener(){
      window.addEventListener('scroll',handleScroll);
    }
    scrollListener();
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  });

    return (
     
      <div className={ScrollActive ? "section01 active" : "section01"}>
        <h1 className='ani'>Our Work</h1>
         <ul className="pubroll__list">
            <li className='ani'>
               <a href="#none">
                 HTML 
               </a>
            </li>
            <li className='ani'>
               <a href="#none">
                 CSS
               </a>
            </li>  
            <li className='ani'>
               <a href="#none">
                 JAVASCRIPT
               </a>
            </li>                       
         </ul>
      </div>

 );
}
export default Sect01;