import React,{Component} from 'react';
import secLimg from '../../assets/img/sect03.jpg';

class Sect03 extends Component {
  render() { 
    return (
     
      <div className="section03">
         <div className='section03__inbox'>
            <div className='section03__img'>              
              <img src= { secLimg } alt="" />
            </div>
            <div className="section03__text">
            시간이 해결해 준다는 말이 있긴 하지만
실제로 일을 변화시켜야 하는 것은
바로 당신이다.<br /><br />
-앤디 워홀-

            </div>
         </div>
      </div>

    )  
  }
}
export default Sect03;